@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.charactersContainer {
  margin: 15px;
  text-align: center;
}
.charactersContainer h1, .charactersContainer h2 {
  color: #B81178;
}
.charactersContainer img {
  border-radius: 15px;
  border: 3px solid #353144;
}

/*# sourceMappingURL=characters.css.map */

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #59358C;
  width: 100%;
  height: 128px;
}
header img {
  width: 96px;
  height: 96px;
  background-color: white;
  border-radius: 25%;
  border: 3px solid #F7AB59;
  margin-left: 15px;
}
header ul {
  display: flex;
  align-items: center;
  list-style: none;
  margin-right: 15px;
}
header ul a {
  text-decoration: none;
  color: #F7AB59;
  margin: 0 10px;
}

@media screen and (max-width: 570px) {
  header {
    justify-content: center;
    flex-direction: column;
    height: 148px;
  }
  header ul {
    flex-direction: column;
    margin: 0;
    position: relative;
    top: 125px;
    left: 10px;
    border: 3px solid #B81178;
    border-radius: 15px;
    padding: 10px;
    font-size: 20px;
  }
  header img {
    position: absolute;
    margin-left: 10px;
    width: 128px;
    height: 128px;
  }
}

/*# sourceMappingURL=header.css.map */

h1 {
    color: #59358C;
}
@media screen and (max-width: 570px) {
    h1 {
        margin-top: 100px;
        text-align: center;
    }
}
form {
  position: relative;
  left: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
  color: #59358C;
  border: 3px solid #59358C;
  width: 500px;
  padding: 15px;
  border-radius: 25px;
}

input {
  width: 250px;
  border: none;
  border-bottom: 3px solid #59358C;
  padding: 0 15px;
  outline: none;
  margin: 10px 0;
}

input::-webkit-input-placeholder {
  color: #59358C;
}

input:-ms-input-placeholder {
  color: #59358C;
}

input::-ms-input-placeholder {
  color: #59358C;
}

input::placeholder {
  color: #59358C;
}

button {
  border: 3px solid #59358C;
  color: #59358C;
  border-radius: 100px;
  width: 200px;
  height: 50px;
  background: none;
  font-size: 15px;
  outline: none;
}

button:hover {
  transition: 300ms;
  transform: scale(1.1);
}

button:active {
  transition: 300ms;
  transform: scale(0.8);
}

@media screen and (max-width: 800px) {
  form {
    left: 20%;
    font-size: 18px;
  }

  input {
    width: 100%;
    margin: 25px 0;
  }

  input::-webkit-input-placeholder {
    font-size: 16px;
  }

  input:-ms-input-placeholder {
    font-size: 16px;
  }

  input::-ms-input-placeholder {
    font-size: 16px;
  }

  input::placeholder {
    font-size: 16px;
  }

  button {
    font-size: 20px;
  }
}
@media screen and (max-width: 570px) {
  form {
    border: none;
    width: 100%;
    top: 100px;
    left: 0;
    width: initial;
  }
}

/*# sourceMappingURL=login.css.map */

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
}

